<template>
  <div class="contain">
    <el-form :model="userForm" :rules="rules" ref="ruleForm" label-width="120px" class="userForm">
      <el-form-item label=":" prop="phone">
        <el-input class="userInput" placeholder="" v-model="userForm.phone"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="password">
        <el-input class="userInput" placeholder="" v-model="userForm.password"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="nickname">
        <el-input class="userInput" placeholder="" v-model="userForm.nickname"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="realName">
        <el-input class="userInput" placeholder="" v-model="userForm.realName"></el-input>
      </el-form-item>
      <el-form-item label="18:" prop="idCard">
        <el-input class="userInput" placeholder="18" v-model="userForm.idCard"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="level">
        <el-select v-model="userForm.level" placeholder="">
          <el-option label="" value="6"></el-option>
          <el-option label="" value="5"></el-option>
          <el-option label="" value="4"></el-option>
          <el-option label="" value="3"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="id:" prop="managerId">
        <el-input class="userInput" placeholder="id" v-model="userForm.managerId"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="isManager">
        <el-select v-model="userForm.isManager" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      userForm: {
        phone: '',
        password: '',
        nickname: '',
        realName: '',
        idCard: '',
        level: '6',
        managerId: '',
        isManager: '0'
      },
      rules: {
        phone: { required: true, message: '', trigger: 'blur' },
        password: { required: true, message: '', trigger: 'blur' },
        nickname: { required: true, message: '', trigger: 'blur' },
        realName: { required: true, message: '', trigger: 'blur' },
        idCard: { required: true, message: '', trigger: 'blur' },
        level: { required: true, message: '', trigger: 'blur' },
        managerId: { required: true, message: 'id', trigger: 'blur' }
      }
    }
  },
  methods: {
    ...mapActions(['addUser']),
    onSubmit () {
      this.$refs['ruleForm'].validate(async (valid) => {
        if (valid) {
          let result = await this.addUser({
            phoneNumber: this.userForm.phone,
            password: this.userForm.password,
            nickname: this.userForm.nickname,
            realName: this.userForm.realName,
            idCard: this.userForm.idCard,
            level: parseInt(this.userForm.level),
            managerId: this.userForm.managerId,
            isManager: this.userForm.isManager === '1'
          })
          if (result.data.code === 200) {
            return this.$message({
              showClose: true,
              type: 'success',
              message: ''
            })
          }
        } else {
          return this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          })
        }
      })
    }
  }
}
</script>
<style lang='less' scoped>
  .contain {
    width: 100%;
    height: 80vh;
    overflow: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 56px;
    .userForm {
      height: 100%;
      .userInput {
        width: 300px;
        height: 50px;
      }
      .form-table {
        width: 200px;
        margin: 0 auto;
      }
    }
  }
</style>



